import React from "react";
import { Link } from "gatsby";
import { getImage, GatsbyImage } from "gatsby-plugin-image";

import "../mobileComponents/mobileNav.css";

const MobileNav = (props) => {

    const image = getImage(props.backgroundImage);

    const hamburgerMenuShow = () => {
        document.getElementById("hiddenMenu").classList.remove("hideHamMenu");
        document.getElementById("hamburgerIconCont").classList.add("hideHam");
    }

    const hamburgerMenuHide = () => {
        document.getElementById("hamburgerIconCont").classList.remove("hideHam");
        document.getElementById("hiddenMenu").classList.add("hideHamMenu");
    }

    const pageReload = (e) => {
        e = e || window.event;
        const target = e.target || e.srcElement;
        const text = target.textContent || target.innerText;
        if (props.currentPage == text) {
            window.location.reload(true);
        }
    }

    return (
        <div className="mobileNavOuter">
            <div className="mobileNav">
                <Link className="mobileHomeImageAndHeaderCont" to="/" onClick={() => pageReload()}>
                    <GatsbyImage image={image} alt="SitesByJack Logo" className="sitesByJackLogoMobile" />
                    <section className="mobileHomeHeaderCont">
                        <p className="mobileHomeHeader">Sites By Jack</p>
                    </section>
                </Link>
                <div className="mobileNavLinks">
                    <div className="mobileNavIndividual" id="homeBullet">
                        <div className="mobileNavBullet"></div>
                        <Link to="/" onClick={() => pageReload()}>
                            Home
                        </Link>
                    </div>
                    <div className="mobileNavIndividual" id="projectsBullet">
                        <div className="mobileNavBullet"></div>
                        <Link to="/projects" onClick={() => pageReload()}>
                            Projects
                        </Link>
                    </div>
                    <div className="mobileNavIndividual" id="aboutBullet">
                        <div className="mobileNavBullet"></div>
                        <Link to="/about" onClick={() => pageReload()}>
                            About Me
                        </Link>
                    </div>
                    <div className="mobileNavIndividual" id="servicesBullet">
                        <div className="mobileNavBullet"></div>
                        <Link to="/services" onClick={() => pageReload()}>
                            Services
                        </Link>
                    </div>
                    <div className="mobileNavIndividual" id="contactBullet">
                        <div className="mobileNavBullet"></div>
                        <Link to="/contact" onClick={() => pageReload()}>
                            Contact
                        </Link>
                    </div>
                </div>
                <div className="hamburgerIconCont" id="hamburgerIconCont" onClick={() => hamburgerMenuShow()}>
                    <div className="hamBar1" />
                    <div className="hamBar2" />
                    <div className="hamBar3" />
                </div>
            </div>
            <div className="hiddenMenu hideHamMenu" id="hiddenMenu">
                <div className="xOutHiddenMenuCont" onClick={() => hamburgerMenuHide()}>
                    <div className="xOut1" />
                    <div className="xOut2" />
                </div>
                <div className="hiddenMenuLinks">
                    <div className="hiddenMenuIndividual" id="homeBullet">
                        <div className="hiddenMenuBullet"></div>
                        <Link to="/" onClick={() => pageReload()}>
                            Home
                        </Link>
                    </div>
                    <div className="hiddenMenuIndividual" id="projectsBullet">
                        <div className="hiddenMenuBullet"></div>
                        <Link to="/projects" onClick={() => pageReload()}>
                            Projects
                        </Link>
                    </div>
                    <div className="hiddenMenuIndividual" id="aboutBullet">
                        <div className="hiddenMenuBullet"></div>
                        <Link to="/about" onClick={() => pageReload()}>
                            About Me
                        </Link>
                    </div>
                    <div className="hiddenMenuIndividual" id="servicesBullet">
                        <div className="hiddenMenuBullet"></div>
                        <Link to="/services" onClick={() => pageReload()}>
                            Services
                        </Link>
                    </div>
                    <div className="hiddenMenuIndividual" id="contactBullet">
                        <div className="hiddenMenuBullet"></div>
                        <Link to="/contact" onClick={() => pageReload()}>
                            Contact
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );

    
}

export default MobileNav;