import React, { useEffect } from "react";
import { Link, graphql } from "gatsby";
import { getImage, GatsbyImage } from "gatsby-plugin-image";

import NavBar from "../components/navbar";
import MobileNav from "../mobileComponents/mobileNav";
import MobileFooter from "../mobileComponents/mobileFooter";
import "../components/aboutComponents/aboutPage.css";
import "../mobileComponents/mobileAbout/mobileAbout.css";
import "../mobileComponents/mobileFooter.css";

const About = ({ data }) => {

    useEffect(() => {
        const element = document.getElementById("aboutBullet");
        element.classList.add("filled");
    })

    const image = getImage(data.mePicture);
    const image2 = getImage(data.mePictureSquare);
    
    return (
        <>
            <section className="aboutCont">
                <section className="aboutLeftHalf">
                    <h1 className="aboutLeftHeader">Who Am I?</h1>
                    <p className="aboutLeftParagraph">
                        Hi! My name is Jack Ludwikoski. I am currently in my first year at the University
                        of Wisconsin-Madison. My Primary area of study is Mechanical Engineering, however
                        I am working towards a minor in Computer Science as well. Website Development fits 
                        into these studies perfectly as it tests both my skills as a designer and my skills
                        with computers.
                    </p>
                </section>
                <section className="aboutRightHalf">
                    <section className="aboutRightUpper">
                        <GatsbyImage image={image} alt="Picture of Jack" className="aboutImage" />
                        <section className="aboutRightTopTextCont">
                            <h1 className="aboutRightHeaderTop">What Skills and Experience Do I Have?</h1>
                            <p className="aboutRightParagraphTop">
                                As of the end of summer 2021, most of my time with designing
                                websites had been focused on learning the various languages and 
                                principles that are foundational to creating quality websites. I had
                                become proficient in HTML, CSS, JavaScript, ReactJS, GatsbyJS, and
                                GraphQL. Since the end of that summer, I have begun to apply these
                                skills to build this site as well as a few other projects. If you are
                                interested in my other work, check out the <Link to="/projects" className="aboutProjectsLink">Projects Page</Link>!
                            </p>
                        </section>
                    </section>
                    <section className="aboutRightLower">
                        <h1 className="aboutRightHeaderBottom">Why Web Development?</h1>
                        <p className="aboutRightParagraphBottom">
                            I have always loved art and science, and in a way, the
                            mixture of computers and code with the precise beauty that lies
                            in the graphic desigin offers me an inerface between these two
                            passions.
                        </p>
                    </section>
                </section>
                <NavBar />
            </section>
            <MobileNav backgroundImage={data.siteLogoDark} currentPage="About Me" />
            <section className="aboutContMobile">
                <section className="aboutMobileImageAndText">
                    <GatsbyImage image={image2} alt="Picture of Jack" className="aboutMobileImage" />
                    <section className="aboutMobileText">
                        <h1 className="aboutMobileH1">Who Am I?</h1>
                        <p className="aboutMobileP1">Hi! My name is Jack Ludwikoski. I am currently in my first year at the University
                            of Wisconsin-Madison. My Primary area of study is Mechanical Engineering, however
                            I am working towards a minor in Computer Science as well. Website Development fits 
                            into these studies perfectly as it tests both my skills as a designer and my skills
                            with computers.</p>
                    </section>
                </section>
                <h1 className="aboutMobileH2">What Skills and Experience Do I Have?</h1>
                <p className="aboutMobileP2">As of the end of summer 2021, most of my time with designing
                    websites had been focused on learning the various languages and 
                    principles that are foundational to creating quality websites. I had
                    become proficient in HTML, CSS, JavaScript, ReactJS, GatsbyJS, and
                    GraphQL. Since the end of that summer, I have begun to apply these
                    skills to build this site as well as a few other projects. If you are
                    interested in my other work, check out the <Link to="/projects" className="aboutProjectsLink">Projects Page</Link>!</p>
                <h1 className="aboutMobileH3">Why Web Development?</h1>
                <p className="aboutMobileP3">I have always loved art and science, and in a way, the
                    mixture of computers and code with the precise beauty that lies
                    in the graphic desigin offers me an inerface between these two
                    passions.</p>
            </section>
            <MobileFooter currentPage="About Me" backToTop={true} />
        </>
    );
}

export const query = graphql`
    query MyAboutQuery {
        mePicture: file(relativeDirectory: {eq: "aboutImages"} name: {eq: "Jack Ludwikoski Senior Picture TouchedUp"})
            {
                childImageSharp {
                    gatsbyImageData(formats: [AUTO, WEBP, AVIF] placeholder: BLURRED width: 400)
                }
            }
        mePictureSquare: file(relativeDirectory: {eq: "aboutImages"} name: {eq: "Jack Ludwikoski Senior Picture TouchedUp (2)"})
        {
            childImageSharp {
                gatsbyImageData(formats: [AUTO, WEBP, AVIF] placeholder: BLURRED width: 500)
            }
        }
        siteLogoDark: file(relativeDirectory: {eq: "homeImages"} name: {eq: "SitesByJackLogo Dark No Background Smaller"}) {
            childImageSharp {
                gatsbyImageData(formats: [AUTO, WEBP, AVIF] placeholder: BLURRED width: 500)
            }
        }
    }
`

export default About