import React from "react"
import { Link } from "gatsby"

import "../components/navbar.css"

const NavBar = () => (
    <div className="navbar">
        <div className="navbarComponents">
            <div className="navbarIndividual" id="homeBullet">
                <div className="navbarBullet"></div>
                <Link to="/">
                    Home
                </Link>
            </div>
            <div className="navbarIndividual" id="projectsBullet">
                <div className="navbarBullet"></div>
                <Link to="/projects">
                    Projects
                </Link>
            </div>
            <div className="navbarIndividual" id="aboutBullet">
                <div className="navbarBullet"></div>
                <Link to="/about">
                    About Me
                </Link>
            </div>
            <div className="navbarIndividual" id="servicesBullet">
                <div className="navbarBullet"></div>
                <Link to="/services">
                    Services
                </Link>
            </div>
            <div className="navbarIndividual" id="contactBullet">
                <div className="navbarBullet"></div>
                <Link to="/contact">
                    Contact
                </Link>
            </div>
        </div>
    </div>
)

export default NavBar