import React, { useEffect } from 'react';
import { Link } from 'gatsby';

const MobileFooter = (props) => {

    useEffect(() => {
        if (!props.backToTop) {
            document.getElementById("backToTopButtonCont").classList.add("hideBackToTop");
            document.getElementById("mobileFooterContactCont").classList.add("fullWidthMobileFooterContactCont");
        }
    })
    
    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        });
    };

    const pageReload = (e) => {
        e = e || window.event;
        const target = e.target || e.srcElement;
        const text = target.textContent || target.innerText;
        if (props.currentPage == text) {
            window.location.reload(true);
        }
    }

    return (
        <section className="mobileFooter">
            <section className="mobileFooterContactCont" id="mobileFooterContactCont" >
                <p className="mobileFooterPhone">Phone Number: <a href="tel:4147318874"><em>414-731-8874</em></a></p>
                <p className="mobileFooterEmail">Email: <a href="mailto:jdludwikoski@gmail.com"><em>jdludwikoski@gmail.com</em></a></p>
            </section>
            <div className="mobileFooterLinks">
                <div className="mobileFooterIndividual" id="homeBullet">
                    <div className="mobileFooterBullet"></div>
                    <Link to="/" onClick={() => pageReload()}>
                        Home
                    </Link>
                </div>
                <div className="mobileFooterIndividual" id="projectsBullet">
                    <div className="mobileFooterBullet"></div>
                    <Link to="/projects" onClick={() => pageReload()}>
                        Projects
                    </Link>
                </div>
                <div className="mobileFooterIndividual" id="aboutBullet">
                    <div className="mobileFooterBullet"></div>
                    <Link to="/about" onClick={() => pageReload()}>
                        About Me
                    </Link>
                </div>
                <div className="mobileFooterIndividual" id="servicesBullet">
                    <div className="mobileFooterBullet"></div>
                    <Link to="/services" onClick={() => pageReload()}>
                        Services
                    </Link>
                </div>
                <div className="mobileFooterIndividual" id="contactBullet">
                    <div className="mobileFooterBullet"></div>
                    <Link to="/contact" onClick={() => pageReload()}>
                        Contact
                    </Link>
                </div>
            </div>
            <section className="backToTopButtonCont" id="backToTopButtonCont">
                <div className="backToTopButton" onClick={() => scrollToTop()}>Back To Top</div>
            </section>
        </section>
    );
}

export default MobileFooter;